import { createRouter, createWebHistory } from 'vue-router'
import HomeSection from '../views/HomeSection.vue'
import DetailList from '../views/DetailList.vue'
import SearchSection from "../views/SearchSection.vue"
import MyList from "../views/MyList.vue"

const routes = [
  {
    path: '/voviepedia',
    name: 'HomeSection',
    component: HomeSection

  },
  {
    path: '/voviepedia/detail/:id',
    name: 'Detail',
    component: DetailList,
    props:true
  },
  {
    path: '/voviepedia/search',
    name: 'Search',
    component: SearchSection   
  },
  {
    path: '/voviepedia/mypage',
    name: 'Mypage',
    component: MyList   
  },
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

export default router
